.assistance__subtitle {
  margin-top: 150px;
}

.main-principle {
  position: relative;
  min-height: 560px;
  margin-top: 150px;
}

.bg_absolute {
  background-image: url('../images/bg_asistans.png');
  background-size: cover;
  padding: 64px 0;
  min-height: 100vh;
}
.head_text {
  margin-top: 30vh;
  letter-spacing: 0.01em;
  color: #ffffff;
}
.offset_top {
  margin-top: 300px;
}

.top_top {
  height: 100%;
}

.assistance__main-principle {
  height: 100%;
}
.loyalty-card__icon {
  max-width: 125%;
}

.absol {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 50%;
  /* height: 560px; */
}

@media (max-width: 991px) {
  .assistance__main-principle {
    position: unset;
    transform: none;
  }

  .absol {
    position: static;
    width: auto;
    height: auto;
  }
}

@media (max-width: 450px) {
  .head_text {
    margin-top: 50px;
  }
}
